import Vue from 'vue'
import Router from 'vue-router'
import LayOut from '@/layout/index'
import utReport from '@/views/report/index'
Vue.use(Router)
// var constantRouterMap=[]



export default new Router({
  routes:[ {
      path: '/report',
      component: LayOut,
      children: [{
        path: 'index',
        component:utReport,
        meta: { title: '兑入', icon: 'fa fa-handshake-o'}
      }]
    },
    {
      path:'*',
      component:404,
      component: () => import('@/views/404'),
    }
  ]
})



