<template>
    <div id="layout">
        <el-container class="el-all">
            <el-header>
                <div class="logo">
                    <img v-if="!isCollapse" src="../assets/img/fex.png" alt="">
                    <img v-else src="../assets/img/fex-img.png" alt="">
                </div>
                <div class="nav-name">
                    <div class="folding" @click="foldingBtn">
                        <i v-if="changeImg" class="fa fa-toggle-on"></i>
                        <i v-else class="fa fa-toggle-off"></i>
                        <span>折叠</span>
                    </div>
                    <div class="login-out">
                        <span>欢迎：{{user}}</span>
                        <span>
                            <i class="fa fa-sign-out"></i>
                            <span>退出</span>
                        </span>
                    </div>
                </div>
            </el-header>
            <el-container>
                <el-aside width="200px">
                    <sidebar :isCollapse="isCollapse"></sidebar>
                </el-aside>
                <el-container class="contant">
                    <el-main>
                        <app-main></app-main>
                    </el-main>
                    <el-footer style="font-size:14px;">
                        © 2019 Tristan-William Global company.版权所有。
                    </el-footer>
                </el-container>
            </el-container>
        </el-container>
    </div>
</template>

<script>
    import sidebar from './component/slide'
    import AppMain from './component/AppMain'
    export default {
        name:'layout',
        data(){
            return{
                user:'tristan',
                isCollapse:false,
                changeImg:false,
                screenWidth: document.body.clientWidth,
            }
        },
        components:{
            sidebar,
            AppMain,
        },
        methods:{
            foldingBtn(){
                this.changeImg = !this.changeImg;
                 if($(window).width()>768){
                    this.isCollapse = !this.isCollapse;
                    $("#layout").toggleClass('change');
                }else{
                    this.isCollapse = false;
                    $("#layout").toggleClass("app");
                }
            },
            handleOpen(key, keyPath) {
                console.log(key, keyPath);
            },
            handleClose(key, keyPath) {
                console.log(key, keyPath);
            }
        },
        mounted(){
            const that = this
            window.onresize = () => {
                return (() => {
                    window.screenWidth = document.body.clientWidth
                    that.screenWidth = window.screenWidth
                })()
            }

        },
        watch: {
            screenWidth(val){
                // 为了避免频繁触发resize函数导致页面卡顿，使用定时器
                if(!this.timer){
                    // 一旦监听到的screenWidth值改变，就将其重新赋给data里的screenWidth
                    this.screenWidth = val
                    this.timer = true
                    let that = this
                    if (this.screenWidth < 768) {
                        that.isCollapse = false;
                        $("#layout").removeClass("app");
                        $("#layout").removeClass("change")
                    }else{
                        that.isCollapse = false;
                        $("#layout").removeClass("app");
                        $("#layout").removeClass("change")
                    }
                    // that.timer = false
                    // setTimeout(function(){
                    //     // 打印screenWidth变化的值
                        
                    // },400)
                }
            },
        }, 
    }
</script>

<style scoped>
.el-header, .el-footer {
    background-color: #fff;
    color: #333;
    text-align: center;
    line-height: 60px;
  }
  
  .el-aside {
    background-color: #0385e5;
    color: #333;
    text-align: center;
    line-height: 200px;
  }
  
  .el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    line-height: 160px;
  }
  
  body > .el-container {
    margin-bottom: 40px;
  }
  
  .el-container:nth-child(5) .el-aside,
  .el-container:nth-child(6) .el-aside {
    line-height: 260px;
  }
  
  .el-container:nth-child(7) .el-aside {
    line-height: 320px;
  }
   .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 201px;
    min-height: 400px;
  }
</style>


