<template>
    <div>
        <el-scrollbar wrap-class="scrollbar-wrapper">
            <el-menu class="el-menu-vertical-demo" 
            mode="vertical"
            @open="handleOpen"
            @close="handleClose" 
            :collapse="isCollapse"
            :unique-opened="true"
            :default-active="$route.path"
            router
            >
                <!-- <side-item :collapse="isCollapse" :routes="routes"></side-item> -->
            <template v-for="item in routes" v-if="!item.hidden&&item.children">
            <router-link v-if="item.children.length===1 && !item.children[0].children && !item.alwaysShow" :to="item.path+'/'+item.children[0].path" :key="item.children[0].name">
                <el-menu-item :index="item.path+'/'+item.children[0].path" :class="{'submenu-title-noDropdown':!isNest}">
                <i :class="item.children[0].meta.icon"></i>
                <span v-if="item.children[0].meta&&item.children[0].meta.title">{{item.children[0].meta.title}}</span>
                </el-menu-item>
            </router-link>

            <el-submenu v-else :index="item.name||item.path" :key="item.name">
                <template slot="title">
                    <i :class="item.meta.icon"></i>
                <span v-if="item.meta&&item.meta.title">{{item.meta.title}}</span>
                </template>

                <template v-for="child in item.children" v-if="!child.hidden">
                <sidebar-item :is-nest="true" class="nest-menu" v-if="child.children&&child.children.length>0" :routes="[child]" :key="child.path"></sidebar-item>

                <router-link v-else :to="item.path+'/'+child.path" :key="child.name">
                    <el-menu-item :index="item.path+'/'+child.path">
                        <i :class="item.children[0].meta.icon"></i>
                        <span v-if="child.meta&&child.meta.title">{{child.meta.title}}</span>
                    </el-menu-item>
                </router-link>
                </template>
            </el-submenu>

        </template>
            </el-menu>
        </el-scrollbar>
    </div>
</template>

<script>
    import sideItem from './sideItem'
    import router from '@/router'
    export default {
        name:'sidebar',
        props:{
            isCollapse: {
                type: Boolean,
                default () {
                    return false
                }
            },
            isNest: {
            type: Boolean,
            default: false
            }
        },
        data(){
            return{
               switchStatusData: this.isCollapse
            }
        },
        components: { sideItem },
        computed: {
            routes() {
                for(var i =0;i<router.options.routes.length;i++){
                    global.antRouter.push(router.options.routes[i])
                }
                // 数组去重
                global.antRouter = [...new Set(global.antRouter)]
                console.log(router.options)
                console.log(global.antRouter)
                return global.antRouter
            }
        
        },
        methods:{
            handleOpen(key, keyPath) {
                console.log(123);
                console.log(key, keyPath);
            },
            handleClose(key, keyPath) {
                console.log(456)
                console.log(key, keyPath);
            }
        }
    }
</script>

<style scoped>

</style>


